"use client";

import { useState } from "react";
import { ICard } from "../types";
import { Item } from "./Item";

interface Props {
  cards: ICard[];
}

export const List = ({ cards }: Props) => {
  const [selectedCard, setSelectedCard] = useState<ICard | undefined>();

  const handleSelectedCard = (card: ICard) => () => {
    setSelectedCard(card);
  };

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4 md:gap-6">
      {cards.length &&
        cards.map((card) => <Item key={`card-${card.id}`} card={card}selectedCard={selectedCard} handleSelectedCard={handleSelectedCard} />)}
    </div>
  );
};
